body {
  margin-top: 105px;
}

.padding-link {
  padding-top: 105px;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}/* Hero */.hero-image {
  height: 412px;
  background: url(../images/group.photo.jpg) 50% center no-repeat;
  background-size: cover;
  width: 100%;
}

.lema {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  display: flex;
  position: relative;
  top: 90%;
  background-color: #ffffff;
  font-family: "Open Sans", sans-serif;
}/* Header */.header {
  width: 100%;
  position: fixed;
  z-index: 100;
  box-sizing: border-box;
}

.header-base {
  display: flex;
  height: 60px;
  opacity: 0.9;
  background-color: #099d8d;
  margin: 0;
  justify-content: center;
  align-items: center;
}

.nombre-del-grupo {
  font-family: Rubik;
  font-size: 16px;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
}

.main-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  height: 45px;
  opacity: 0.9;
  background-color: #14d9c5;
  margin: 0;
  padding: 0 15px 0;
  text-transform: uppercase;
}

.main-bar a {
  font-family: Rubik;
  text-decoration: none;
  color: white;
  padding: 8px;
}/* About */.team {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 0px;
  position: relative;
}

.team-title {
  font-family: Rubik;
  font-size: 24px;
  font-weight: bold;
  justify-content: center;
  color: #14d9c4;
  margin-bottom: 15px;
  text-transform: uppercase;
}

.team-content {
  justify-content: center;
  text-align: center;
  line-height: 1.5;
  margin: 15px;
  margin-bottom: 30px;
  font-family: "open sans", sans-serif;
  font-size: 16px;
  color: #54585a;
}

.team .decoration-box {
  width: 30px;
  height: 30px;
  transform: rotate(45deg);
  background-color: #ffffff;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  bottom: -15px;
}

body {
  margin: 0;
}

.features {
  background-color: #f1f1f1;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 20px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 20px;
}

.features-features-container {
  width: 100%;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
}

.features-content {
  width: 100%;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
  font-family: "open sans", sans-serif;
  font-size: 16px;
  color: #54585a;
}

.features-title {
  color: #099d8d;
  font-family: "Rubik", sans-serif;
  font-size: 22px;
  margin-top: 10px;
  text-align: center;
}/* team */.adalabers {
  padding-top: 10px;
}

.adalaber {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.quienes {
  width: 193px;
  height: 45px;
  font-family: Rubik;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.88;
  color: #099d8d;
  margin: 0 auto;
  text-align: center;
  text-transform: uppercase;
}

.name {
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 20px;
  line-height: 2.25;
  color: #000000;
  text-align: center;
}

.text {
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #54585a;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
}

.photo {
  width: 100%;
}

.photobox {
  width: 180px;
  height: 180px;
  border: solid #b8b8b9 5px;
  border-radius: 4px;
  box-sizing: content-box;
  margin: 10px;
  padding: 0px;
}

body {
  display: block;
}/* ESTILOS ICONOS */.fa-twitter {
  color: #14d9c4;
}/* PUNTO TRAS TWITTER */.fa-twitter:after {
  content: "\002E";
  position: relative;
  top: -4px;
  left: 8px;
  color: black;
}

.fa-linkedin-in {
  color: #14d9c4;
}

.fa-linkedin-in-odd {
  color: #14d9c4;
}/* PUNTO TRAS LINKEDIN */.fa-linkedin-in:after {
  content: "\002E";
  position: relative;
  top: -4px;
  left: 8px;
  color: black;
}

.fa-linkedin-in-odd:after {
  content: "\002E";
  position: relative;
  top: -4px;
  left: 12px;
  color: black;
}

.fa-github-alt {
  color: #14d9c4;
}

.fa-github-alt-odd {
  color: #14d9c4;
}/* PUNTO TRAS GITHUB */.fa-github-alt:after {
  content: "\002E";
  position: relative;
  top: -4px;
  left: 8px;
  color: black;
}

.fa-github-alt-odd:after {
  content: "\002E";
  position: relative;
  top: -4px;
  left: 12px;
  color: black;
}

.fa-envelope {
  color: #14d9c4;
}

.icons {
  width: 124px;
  height: 30px;
  margin: 0 auto;
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
}/* footer */.container-footer {
  width: 100%;
  border-top: solid 1px #f1f1f1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.copyright {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  color: #b8b8b9;
  margin-top: 30px;
  margin-bottom: 20px;
}

.links-footer {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #54585a;
  list-style: none;
  text-align: center;
  padding: 0px;
  margin-bottom: 30px;
  margin-top: 0px;
  text-transform: uppercase;
}

.adalab-logo {
  width: 115.9px;
  height: 45px;
  margin-bottom: 45px;
}

.links-footer a {
  color: #54585a;
}

a {
  text-decoration: none;
}/* @media Tablet */@media (min-width: 768px) {
  .header {
    display: flex;
    flex-direction: row;
    background-color: #099d8d;
    width: 100%;
    height: 60px;
    justify-content: space-between;
  }

  .header-base {
    justify-content: flex-start;
    background-color: transparent;
    padding: 15px 45px;
  }

  .main-bar {
    justify-content: space-between;
    height: 60px;
    background-color: #099d8d;
    padding: 0 36px 0;
  }

  .main-bar a {
    padding: 9px;
  }
}

@media all and (min-width: 768px) {
  .features {
    flex-direction: row;
  }

  .features-container {
    width: 50%;
    margin-right: 45px;
    margin-left: 45px;
  }

  .team {
    margin-left: 45px;
    margin-right: 45px;
  }

  .adalaber-even {
    display: flex;
    flex-direction: row;
  }

  .adalaber-odd {
    display: flex;
    flex-direction: row-reverse;
  }

  .information {
    width: auto;
    display: flex;
    flex-direction: column;
    align-items: left;
  }

  .photobox-even {
    min-width: 205px;
    min-height: 205px;
    margin-left: 44px;
    margin-right: 31px;
  }

  .photobox-odd {
    min-width: 205px;
    min-height: 205px;
    margin-left: 31px;
    margin-right: 44px;
  }

  .text-even {
    text-align: left;
    margin-right: 44px;
    padding: 0;
  }

  .text-odd {
    text-align: right;
    margin-left: 44px;
    padding: 0;
  }

  .name-even {
    text-align: left;
  }

  .name-odd {
    text-align: right;
  }

  .photo {
    width: 205px;
    height: 205px;
  }

  .icons-even {
    margin-left: 0;
  }

  .icons-odd {
    margin-right: 0;
  }

  .container p {
    margin-top: 0px;
    margin-left: 30px;
    margin-right: 30px;
    margin-bottom: 60px;
  }

  .container-footer {
    height: 170px;
    padding-top: 30px;
    padding-left: 45px;
    padding-right: 45px;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  .copyright {
    width: 50%;
    margin: 0;
  }

  .links-footer {
    width: 50%;
    line-height: 30px;
    text-align: right;
    margin: 0;
  }

  .adalab-logo {
    margin-top: -75px;
    margin-bottom: 0px;
  }
}

@media (min-width: 1200px) {
  .lema {
    width: 48%;
    margin-left: 138px;
    margin-right: 0;
    padding: 15px 30px;
    top: 88.5%;
    opacity: 80%;
  }

  .hero-image {
    height: 550px;
  }

  .header-base {
    padding: 15px 138px;
  }

  .main-bar {
    padding: 0 129px 0;
  }
}

@media all and (min-width: 1200px) {
  .features {
    flex-direction: row;
  }

  .features-container {
    width: 50%;
    margin-right: 138px;
